.body {

    background-color : coral;
    position : fixed;
    left : 100px;
}
.main_div {
    position : fixed;
    left : 40%;
    top : 100px;
}
.header {
    font-weight: bold;
    font-size: 25px;
    padding : 15px;
    margin : 20px;
}
.input {
    border : 2px solid black;
    outline : none;
    border-radius : 15px;
    padding : 7px;
    font-weight:  bold;
    
}
.add_button {
    border-radius: 15px;
    margin-left : 15px;
    font-size : 15px;
    position : fixed;
    top : 197px;
}
.todos_list {  
    position : relative;
}
.todos {
    background-color : purple;
    border : 2px solid black;
    border-radius: 15px;
    width : fit-content;
    color: white;
    margin : 10px;
    padding-left : 10px;

}
.remove_button {
    border : 2px solid black;
    border-radius : 15px;
    margin : auto;
    position : relative;
    left : 100px;
    
}